<template>
  <view class="container">
    <u-form :model="form" ref="forgotPasswordForm">
      <u-form-item  prop="email">
        <u-input v-model="form.email" placeholder="请输入邮箱" />
      </u-form-item>
      <u-button type="primary" @click="handleForgotPassword">提交</u-button>
      <u-button type="default" @click="goToLogin">返回登录</u-button>
    </u-form>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        email: "",
      },
    };
  },
  methods: {
    // 处理找回密码
    handleForgotPassword() {
      if (!this.form.email) {
        uni.showToast({
          title: "请输入邮箱",
          icon: "none",
        });
        return;
      }
      // 模拟提交成功
      uni.showToast({
        title: "重置密码邮件已发送",
        icon: "success",
      });
      uni.navigateBack();
    },
    // 跳转到登录页面
    goToLogin() {
      uni.navigateTo({
        url: "/pages/login/login",
      });
    },
  },
};
</script>

<style>
.container {
   padding:70px 40px ;
}
</style>